.offset {
  box-shadow: 0.3em 0.3em 0 0 var(--color), inset 0.3em 0.3em 0 0 var(--color);
  font-size: 15px;
}
.offset:hover,
.offset:focus {
  box-shadow: 0 0 0 0 var(--hover), inset 6em 3.5em 0 0 var(--hover);
}
.offset {
  --color: #273e47;
  --hover: #db5461;
}
button {
  color: var(--color);
  transition: 0.25s;
  width: 200px;
}
button:focus {
